.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

html {
  overflow: hidden; }

#nav-menu {
  height: 50px; }

#main-nav {
  box-shadow: 0px 1px 5px #818181; }

#footer {
  position: fixed;
  min-height: 50px;
  bottom: 0;
  left: 0;
  z-index: 50000;
  width: 100%; }

#date-select-slider-container {
  background: rgba(255, 255, 255, 0.9);
  border: 2px solid rgba(0, 0, 0, 0.2);
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
  padding: 5px 15px;
  border-radius: 10px 10px 0 0; }
  #date-select-slider-container input[type=range] {
    height: 35px;
    display: block;
    width: 100%; }
  #date-select-slider-container .title {
    font-weight: bold;
    text-transform: capitalize; }

.leaflet-marker-icon {
  opacity: 0.85; }
  .leaflet-marker-icon:hover {
    opacity: 1; }

#legend {
  position: fixed;
  right: 5px;
  top: 65px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.9);
  z-index: 1000;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 3px; }
  #legend:not(.expanded) .legend-entry {
    display: none; }
  #legend .expand-button {
    float: right; }
  #legend .color-block {
    width: 30px;
    height: 15px;
    display: inline-block;
    margin-right: 5px;
    border: 1px solid #6b6b6b; }

#about {
  max-width: 650px;
  margin: 0 auto;
  margin-top: 25px; }
  #about h4 {
    margin-top: 25px; }
  #about p {
    margin-bottom: 20px; }

.leaflet-popup-content .all-data thead tr th:not(:first-child) {
  width: 150px; }
.leaflet-popup-content .point-name {
  font-weight: bold;
  text-align: center;
  width: 100%;
  border-bottom: 1px solid lightgrey;
  padding-bottom: 5px;
  margin-bottom: 10px; }
